/* Render the `main` element consistently in IE */
main {
  display: block;
}

/* Pico */
/* –––––––––––––––––––– */

/* 1. Remove the margin in all browsers (opinionated) */
body {
  width: 100%;
  margin: 0; /* 1 */

  > header,
  > main,
  > footer {
    width: 100%;
    margin-right: auto;
    margin-left: auto;
  }
}
